import React, { Component } from "react";
import Collapsible from "react-collapsible";
import "./headers.css";

class FrameWork extends Component {
  render() {
    return (
      <Collapsible trigger="Framework">
        <ul className="indenting">
          <li>React</li>
          <li>Android</li>
        </ul>
      </Collapsible>
    );
  }
}

export default FrameWork;
