import React, { Component } from "react";
import Collapsible from "react-collapsible";
import "./headers.css";

class Technology extends Component {
  render() {
    return (
      <Collapsible trigger="Technology">
        <ul className="indenting">
          <li>MySQL</li>
          <li>Latex</li>
          <li>Git</li>
          <li>Adobe XD</li>
          <li>XCode</li>
        </ul>
      </Collapsible>
    );
  }
}

export default Technology;
