import React, { Component } from "react";
import Collapsible from "react-collapsible";
import "./headers.css";
class Lan extends Component {
  render() {
    return (
      <Collapsible trigger=" Languages">
        <ul className="indenting">
          <li>Javascript</li>
          <li>Java</li>
          <li>Python</li>
          <li>SQL</li>
          <li>Swift</li>
        </ul>
      </Collapsible>
    );
  }
}

export default Lan;
